<template>
  <card>
    <h4 slot="header" class="card-title">Agregar Producto</h4>
    <form>
      <!-- Campos para el producto -->
      <div class="row">
        <div class="col-md-6">
          <base-input type="text" label="Nombre del Producto" placeholder="Nombre del Producto"
            v-model="product.nombre">
          </base-input>
        </div>
        <div class="col-md-6">
          <!-- Botones para subir o tomar la foto del producto -->
          <div>
            <label>Imagen del Producto</label>
            <button @click="showCamera" class="btn btn-primary">Tomar Foto</button>
            <div class="modal" v-show="showCameraModal">
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-header">
                    <h5 class="modal-title">Cámara</h5>
                    <button type="button" class="close" @click="closeCameraModal">
                      <span>&times;</span>
                    </button>
                  </div>
                  <div class="modal-body">
                    <video ref="video" autoplay style="max-width: 100%; height: auto;"></video>
                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" @click="capturePhoto">Capturar Foto</button>
                    <button type="button" class="btn btn-danger" @click="closeCameraModal">Cerrar Cámara</button>
                  </div>
                </div>
              </div>
            </div>
            <img :src="product.imgProduct" alt="Imagen del Producto" v-if="product.imgProduct"
              style="max-width: 100px;">
          </div>
        </div>
      </div>

      <!-- Campo para la clasificación -->
      <div class="row">
        <div class="col-md-6">
          <BaseSelect :options="categories" label="Clasificación" placeholder="Selecciona la clasificación"
            v-model="product.clasificacion" @input="handleCategoryChange"></BaseSelect>


        </div>
        <div class="col-md-6">
          <BaseMultiSelect :options="flavors[product.category]" label="Sabores" placeholder="Selecciona los sabores"
            v-model="product.flavors"></BaseMultiSelect>

        </div>
      </div>

      <!-- Campo para el código de barras -->
      <div class="row">
        <div class="col-md-6">
          <base-input type="text" label="Código de Barras" placeholder="Código de Barras" v-model="product.barcode"
            readonly>
          </base-input>
          <button @click="generateBarcode" class="btn btn-secondary">Generar Código de Barras</button>
          <div v-if="product.barcode">
            <barcode :value="product.barcode"></barcode>
            <button @click="printBarcode" class="btn btn-secondary">Imprimir Código de Barras</button>
          </div>
        </div>
        <div class="col-md-6">
          <base-input type="date" label="Fecha de Caducidad" placeholder="Fecha de Caducidad"
            v-model="product.caducidad">
          </base-input>
        </div>
      </div>

      <!-- Campos para precio y cantidad en stock -->
      <div class="row">
        <div class="col-md-6">
          <base-input type="number" label="Precio" placeholder="Precio" v-model="product.precio">
          </base-input>
        </div>
        <div class="col-md-6">
          <base-input type="number" label="Cantidad en Stock" placeholder="Cantidad en Stock" v-model="product.stock">
          </base-input>
        </div>
      </div>

      <!-- Campo para seleccionar tamaño del vaso de helado -->
      <div class="row">
        <div class="col-md-6">
          <BaseSelect :options="cupSizes" label="Tamaños del Vaso de Helado" placeholder="Selecciona el tamaño del vaso"
            v-model="product.cupSizes"></BaseSelect>
        </div>
      </div>

      <!-- Botón para agregar producto -->
      <div class="text-center">
        <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="addProduct">
          Agregar Producto
        </button>
      </div>
      <div class="clearfix"></div>
    </form>
  </card>
</template>

<script>
import Card from 'src/components/Cards/Card.vue';
import BaseInput from 'src/components/Inputs/BaseInput.vue';
import BaseSelect from 'src/components/Inputs/BaseSelect.vue';
import BaseMultiSelect from 'src/components/Inputs/BaseMultiSelect.vue';
import Barcode from 'vue-barcode';
import imageCompression from 'browser-image-compression';
import moment from 'moment';

export default {
  components: {
    Card,
    BaseInput,
    BaseSelect,
    BaseMultiSelect,
    Barcode,
    imageCompression,
    moment
  },
  data() {
    return {
      product: {
        nombre: '',
        imgProduct: '',
        barcode: '',
        barcodeimg: '',
        precio: 0,
        stock: 0,
        caducidad: '',
        clasificacion: 0,
        category: '',
        formattedDate : moment().format('YYYY-MM-DD HH:mm:ss'),
        sabores: '',
        litros: 0,
        tasty: '',
        flavors: [],
        cupSizes: '',
        IceCreamSizes: ''
      },
      categories: [
        { value: 1, text: 'Bebidas' },
        { value: 2, text: 'Helados' },
        { value: 3, text: 'Troles' },
        { value: 4, text: 'Vasos' },
        { value: 5, text: 'Otros' },
      ],
      flavors: {
        Helados: [
          { value: 1, text: 'Vainilla' },
          { value: 2, text: 'Chocolate' },
          { value: 3, text: 'Fresa' },
          { value: 4, text: 'Limón' },
        ],
        Bebidas: [
          { value: 1, text: 'Coca-Cola' },
          { value: 2, text: 'Sprite' },
          { value: 3, text: 'Fanta' },
          { value: 4, text: 'Pepsi' },
        ],
        Troles: [
          { value: 1, text: 'Tamarindo' },
          { value: 2, text: 'Mango' },
          { value: 3, text: 'Piña' },
          { value: 4, text: 'Fresa' },
        ],
        Vasos: [
          { value: 1, text: 'Tamarindo' },
          { value: 2, text: 'Mango' },
          { value: 3, text: 'Piña' },
          { value: 4, text: 'Fresa' },
        ],
      },
      cupSizes: [
        { value: '1 litro', text: 'Grande (1 litro)' },
        { value: '0.5', text: 'Mediano (0.5 litros)' },
        { value: '0.090', text: 'Chico (0.090 a 0.180 litros)' },
      ],
      IceCreamSizes: [
        { value: '1 litro', text: 'Grande (1 litro)' },
        { value: '0.5', text: 'Mediano (0.5 litros)' },
        { value: '0.090', text: 'Chico (0.090 a 0.180 litros)' },
      ],
      selectedCupSize: '',
      showCameraModal: false,
      videoStream: null,
    };
  },
  methods: {
    generateBarcode() {
      this.drawImage.barcode = Math.random().toString(36).substring(2, 12).toUpperCase();
    },
    printBarcode() {
      const printWindow = window.open('', '_blank');
      printWindow.document.write(`<img src="${this.$refs.barcode.toDataURL()}">`);
      printWindow.document.close();
      printWindow.print();
    },
    handleCategoryChange(category) {
      console.log(category)
      this.product.clasificacion = category;
      // Actualiza los sabores correspondientes a la nueva categoría seleccionada
      this.product.flavors = this.flavors[category] || [];
    },
    showCamera() {
      this.showCameraModal = true;
      navigator.mediaDevices.getUserMedia({ video: true })
        .then((stream) => {
          this.videoStream = stream;
          this.$refs.video.srcObject = stream;
        })
        .catch((error) => {
          console.error('Error accessing camera: ', error);
        });
    },
    async compressImage(file) {
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 500,
        useWebWorker: true
      };
      try {
        const compressedFile = await imageCompression(file, options);
        return compressedFile;
      } catch (error) {
        console.error('Error during image compression:', error);
        return file;
      }
    },
    async capturePhoto() {
      const canvas = document.createElement('canvas');
      canvas.width = this.$refs.video.videoWidth;
      canvas.height = this.$refs.video.videoHeight;
      const context = canvas.getContext('2d');
      context.drawImage(this.$refs.video, 0, 0, canvas.width, canvas.height);

      // Convert canvas to Blob
      canvas.toBlob(async (blob) => {
        const compressedBlob = await this.compressImage(blob);
        const reader = new FileReader();
        reader.onloadend = () => {
          this.product.imgProduct = reader.result;
        };
        reader.readAsDataURL(compressedBlob);
      }, 'image/jpeg', 0.7); // 'image/jpeg' para asegurar la compresión
      this.closeCameraModal();
    },
    closeCameraModal() {
      this.showCameraModal = false;
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
      }
    },
    generateBarcode() {
      this.product.barcode = Math.random().toString(36).substring(2, 12).toUpperCase();
    },
    printBarcode() {
      const printWindow = window.open('', '_blank');
      printWindow.document.write(`<img src="${this.$refs.barcode.toDataURL()}">`);
      printWindow.document.close();
      printWindow.print();
    },
    addProduct() {
      // Envía los datos del producto al backend para ser agregados a la base de datos
      console.log(this.product)
      fetch('http://localhost:3000/api/products', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(this.product),
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('Failed to add product');
          }
          return response.json();
        })
        .then(data => {
          alert('Producto agregado correctamente');
          console.log('Producto agregado:', data);
          // Aquí puedes realizar otras acciones después de agregar el producto, como limpiar el formulario o redirigir a otra página
        })
        .catch(error => {
          console.error('Error al agregar producto:', error);
          alert('Error al agregar producto');
        });
    },
    sellIceCream() {
      let quantityToDeduct = 0;
      if (this.selectedCupSize === 'grande') {
        quantityToDeduct = 1;
      } else if (this.selectedCupSize === 'mediano') {
        quantityToDeduct = 0.5;
      } else if (this.selectedCupSize === 'chico') {
        // Aquí puedes agregar una lógica adicional para manejar la cantidad variable del tamaño chico
        quantityToDeduct = parseFloat(prompt("Ingresa la cantidad para el vaso chico (0.090 a 0.180 litros):"));
        if (isNaN(quantityToDeduct) || quantityToDeduct < 0.090 || quantityToDeduct > 0.180) {
          alert("Cantidad inválida. Por favor, ingresa un valor entre 0.090 y 0.180 litros.");
          return;
        }
      }
      if (this.product.stock >= quantityToDeduct) {
        this.product.stock -= quantityToDeduct;
        alert(`Se vendió un vaso de helado ${this.selectedCupSize} y se descontaron ${quantityToDeduct} litros del stock.`);
      } else {
        alert("No hay suficiente stock para vender este tamaño de vaso.");
      }
    },
  },
};
</script>

<style>
