  display: block;
  background: rgba(0, 0, 0, 0.5);
}

.modal-dialog {
  margin: 15% auto;
  width: fit-content;
}

.modal-content {
  background-color: white;
  border-radius: 10px;
}

.modal-header {
  border-bottom: none;
}

.modal-body {
  text-align: center;
}

.modal-footer {
  border-top: none;
}
</style>