<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card>
            <template slot="header">
              <h4 class="card-title">Inventario Disponible</h4>
              <button class="btn btn-success float-right" @click="openAddModal">Agregar Insumo</button>
            </template>
            <!-- Campos de búsqueda -->
            <div class="form-group row">
              <div class="col-md-6">
                <input type="text" class="form-control" v-model="searchName" placeholder="Buscar por nombre">
              </div>
              <div class="col-md-6">
                <input type="text" class="form-control" v-model="searchBarcode"
                  placeholder="Buscar por código de barras">
              </div>
            </div>
            <!-- Lista de productos filtrados -->
            <div class="row">
              <div v-for="product in filteredProducts" :key="product.id" class="col-md-4 mb-3">
                <div :class="['card', productClass(product)]">
                  <img v-if="product.imgProduct" :src="getImageUrl(product.imgProduct)" class="card-img-top" alt="Imagen del Inventario">
                  <div class="card-body">
                    <h5 class="card-title">{{ product.nombre }}</h5>
                    <br>
                    <p class="card-text">Precio: {{ product.precio | currency }}</p>
                    <p class="card-text">Stock: {{ product.stock }}</p>
                    <p class="card-text">Caducidad: {{ product.caducidad }}</p>
                    <button class="btn btn-primary" @click="openEditModal(product)">{{ buttonStatus(product) }}</button>
                    <button class="btn btn-danger" @click="openDeleteModal(product)">Eliminar</button>
                  </div>
                </div>
              </div>
            </div>
          </card>
        </div>
      </div>
    </div>
    <div class="modal" id="addModal" role="dialog" v-if="addModal">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Agregar Insumo</h5>
            <button type="button" class="close" @click="closeModalAdd" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body modal-scrollable">
            <div class="form-group">
              <label>Nombre</label>
              <input type="text" class="form-control" v-model="newProduct.nombre">
            </div>
            <div class="form-group">
              <label>Tipo</label>
              <input type="text" class="form-control" v-model="newProduct.clasificacion">
            </div>
            <div class="form-group">
              <label>Cantidad</label>
              <input type="number" class="form-control" v-model="newProduct.stock">
            </div>
            <div class="form-group">
              <label>Precio</label>
              <input type="number" class="form-control" v-model="newProduct.precio">
            </div>
            <div class="form-group">
              <label>Caducidad</label>
              <input type="date" class="form-control" v-model="newProduct.caducidad">
            </div>
            <div class="form-group">
              <base-input type="text" label="Código de Barras" placeholder="Código de Barras"
                v-model="newProduct.barcode" readonly></base-input>
            </div>
            <div class="form-group">
              <button @click="generateBarcode" class="btn btn-secondary">Generar Código de Barras</button>
              <div v-if="newProduct.barcode">
                <barcode :value="newProduct.barcode"></barcode>
                <button @click="printBarcode" class="btn btn-secondary">Imprimir Código de Barras</button>
              </div>
            </div>
            <div>
              <button @click="showCamera" class="btn btn-primary">Tomar Foto</button>
              <div class="modal" v-show="showCameraModal">
                <div class="modal-dialog">
                  <div class="modal-content">
                    <div class="modal-header">
                      <h5 class="modal-title">Cámara</h5>
                      <button type="button" class="close" @click="closeCameraModal">
                        <span>&times;</span>
                      </button>
                    </div>
                    <div class="modal-body">
                      <video ref="video" autoplay style="max-width: 100%; height:
                      auto;"></video>
                    </div>
                    <div class="modal-footer">
                      <button type="button" class="btn btn-secondary" @click="capturePhoto">Capturar Foto</button>
                      <button type="button" class="btn btn-danger" @click="closeCameraModal">Cerrar Cámara</button>
                    </div>
                  </div>
                </div>
              </div>
              <img :src="newProduct.imageUrl" alt="Imagen del Producto" v-if="newProduct.imageUrl"
                style="max-width: 100px;">
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="addProduct">Guardar</button>
            <button type="button" class="btn btn-secondary" @click="closeModalAdd">Cerrar</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal para actualizar stock -->
    <div class="modal" id="editModal" tabindex="-1" role="dialog" v-if="editModal">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Actualizar Stock</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p v-if="selectedProduct">Ingrese la nueva cantidad de stock para {{ selectedProduct.name }}:</p>
            <input type="number" class="form-control" v-model="newStock">
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" @click="saveStockEdit">Guardar</button>
            <button type="button" class="btn btn-secondary" @click="closeModalEdit">Cerrar</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal para eliminar producto -->
    <div class="modal" id="deleteModal" tabindex="-1" role="dialog" v-if="deleteModal">
      <div class="modal-dialog       " role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Eliminar Producto</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>¿Estás seguro de que deseas eliminar el producto "{{ selectedProduct.nombre }}"?</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger" @click="deleteProduct">Eliminar</button>
            <button type="button" class="btn btn-secondary" @click="closeModalDelete">Cancelar</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Barcode from 'vue-barcode';
import imageCompression from 'browser-image-compression';

export default {
  components: {
    Barcode,
    imageCompression 
  },
  data() {
    return {
      products: [],
      searchName: '',
      searchBarcode: '',
      selectedProduct: null,
      newStock: null,
      deleteModal: false,
      editModal: false,
      addModal: false,
      showCameraModal: false,
      videoStream: null,
      newProduct: {
        id: 0,
        nombre: '',
        precio: '',
        stock: 0,
        barcode: '',
        barcodeimg: '',
        imgProduct: '',
        caducidad: '',
        clasificacion: 0,
        sabores: '',
        litros: 0
      }
    };
  },
  computed: {
    filteredProducts() {
      let filtered = [...this.products];
      if (this.searchName) {
        filtered = filtered.filter(product => product.nombre.toLowerCase().includes(this.searchName.toLowerCase()));
      }
      if (this.searchBarcode) {
        filtered = filtered.filter(product => product.barcode === this.searchBarcode);
      }
      return filtered;
    }
  },
  methods: {
    getImageUrl(imgProduct) {
      // Creamos un blob a partir del buffer
      const blob = new Blob([new Uint8Array(imgProduct.data)], { type: 'image/jpeg' });
      // Creamos una URL para el blob
      return URL.createObjectURL(blob);
    },
    productClass(product) {
      if (product.stock === 0) {
        return 'border-danger'; // Rojo si no hay stock
      } else if (product.stock <= 5) {
        return 'border-warning'; // Amarillo si hay poco stock
      } else if (this.isExpired(product.expiryDate)) {
        return 'border-dark'; // Negro si está por caducar
      } else {
        return 'border-success'; // Verde si hay suficiente stock y no está por caducar
      }
    },
    isExpired(expiryDate) {
      const today = new Date();
      const expiry = new Date(expiryDate);
      return expiry < today; // Caducado si la fecha de caducidad es anterior al día de hoy
    },
    buttonStatus(product) {
      if (this.isExpired(product.expiryDate)) {
        return 'Caducado';
      } else if (this.isExpiringSoon(product.expiryDate)) {
        return 'Caducará pronto';
      } else if (product.stock === 0) {
        return 'Sin stock';
      } else if (product.stock <= 5) {
        return 'Poco stock';
      } else {
        return 'Todo bien';
      }
    },
    isExpiringSoon(expiryDate) {
      const today = new Date();
      const expiry = new Date(expiryDate);
      const diffTime = expiry.getTime() - today.getTime();
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      return diffDays <= 7 && !this.isExpired(expiryDate); // A punto de caducar si quedan menos de 7 días
    },
    openEditModal(product) {
      this.selectedProduct = product;
      this.newStock = product.stock;
      this.editModal = true;
    },
    closeModalDelete() {
      this.deleteModal = false;
    },
    closeModalEdit() {
      this.editModal = false;
    },
    saveStockEdit() {
      if (this.selectedProduct && this.newStock !== null && !isNaN(this.newStock) && this.newStock >= 0) {
        axios.put(`http://localhost:3000/api/Inventory/${this.selectedProduct.id}`, { stock: this.newStock })
          .then(() => {
            this.selectedProduct.stock = this.newStock;
            this.editModal = false;
          })
          .catch(error => {
            console.error('Error updating stock:', error);
          });
      }
    },
    openDeleteModal(product) {
      this.selectedProduct = product;
      this.deleteModal = true;
    },
    deleteProduct() {
      const index = this.products.findIndex(prod => prod.id === this.selectedProduct.id);
      if (index !== -1) {
        axios.delete(`http://localhost:3000/api/Inventory/${this.selectedProduct.id}`)
          .then(() => {
            this.products.splice(index, 1);
            this.deleteModal = false;
          })
          .catch(error => {
            console.error('Error deleting product:', error);
          });
      }
    },
    showCamera() {
      this.showCameraModal = true;
      navigator.mediaDevices.getUserMedia({ video: true })
        .then((stream) => {
          this.videoStream = stream;
          this.$refs.video.srcObject = stream;
        })
        .catch((error) => {
          console.error('Error accessing camera: ', error);
        });
    },
    async compressImage(file) {
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 500,
        useWebWorker: true
      };
      try {
        const compressedFile = await imageCompression(file, options);
        return compressedFile;
      } catch (error) {
        console.error('Error during image compression:', error);
        return file;
      }
    },
    async capturePhoto() {
      const canvas = document.createElement('canvas');
      canvas.width = this.$refs.video.videoWidth;
      canvas.height = this.$refs.video.videoHeight;
      const context = canvas.getContext('2d');
      context.drawImage(this.$refs.video, 0, 0, canvas.width, canvas.height);
      
      // Convert canvas to Blob
      canvas.toBlob(async (blob) => {
        const compressedBlob = await this.compressImage(blob);
        const reader = new FileReader();
        reader.onloadend = () => {
          this.newProduct.imgProduct = reader.result;
        };
        reader.readAsDataURL(compressedBlob);
      }, 'image/jpeg', 0.7); // 'image/jpeg' para asegurar la compresión
      this.closeCameraModal();
    },
    closeCameraModal() {
      this.showCameraModal = false;
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
      }
    },
    generateBarcode() {
      this.newProduct.barcode = Math.random().toString(36).substring(2, 12).toUpperCase();
    },
    printBarcode() {
      const printWindow = window.open('', '_blank');
      printWindow.document.write(`<img src="${this.$refs.barcode.toDataURL()}">`);
      printWindow.document.close();
      printWindow.print();
    },
    openAddModal() {
      this.addModal = true;
    },
    closeModalAdd() {
      this.addModal = false;
    },
    addProduct() {
      console.log(this.newProduct);

      axios.post('http://localhost:3000/api/Inventory', this.newProduct)
        .then(response => {
          this.products.push(response.data);
          this.closeModalAdd();
        })
        .catch(error => {
          console.error('Error adding product:', error);
        });
    },
    fetchProducts() {
      axios.get('http://localhost:3000/api/Inventory')
        .then(response => {
          this.products = response.data;
        })
        .catch(error => {
          console.error('Error fetching inventory data:', error);
        });
    }
  },
  created() {
    this.fetchProducts();
  },
  filters: {
    currency(value) {
      return new Intl.NumberFormat('es-MX', { style: 'currency', currency: 'MXN' }).format(value);
    }
  }
};
</script>

<style>
.card {
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 20px;
}

.border-danger {
  border-color: red;
}

.border-warning {
  border-color: yellow;
}

.border-dark {
  border-color: black;
}

.border-success {
  border-color: green;
}

.modal-dialog {
  position: fixed;
  top: 50%;
  /* Centra verticalmente */
  left: 50%;
  /* Centra horizontalmente */
  transform: translate(-50%, -50%);
  /* Desplaza el modal hacia arriba y hacia la izquierda en la mitad de su tamaño */
  margin: 0;
  /* Elimina cualquier margen */
}

.modal-scrollable {
  max-height: calc(100vh - 200px);
  /* Altura máxima menos el espacio para el encabezado y el pie de página */
  overflow-y: auto;
  /* Permitir desplazamiento vertical si el contenido excede la altura máxima */
}
</style>
