<template>
  <div class="form-group">
    <label :for="name">{{ label }}</label>
    <select class="form-control" :id="name" v-model="selectedOption">
      <option v-for="option in options" :key="option.value" :value="option.value">
        {{ option.text }}
      </option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'BaseMultiSelect',
  props: {
    value: {
      type: String, // Cambiado a String
      default: ''
    },
    label: {
      type: String,
      required: true
    },
    options: {
      type: Array,
      required: true
    },
    name: {
      type: String,
      default: 'multi-select'
    }
  },
  computed: {
    selectedOption: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit('input', newValue);
      }
    }
  }
};
</script>

<style scoped>
.form-group {
  margin-bottom: 1rem;
}
</style>
