<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <!-- Lista de productos -->
        <div class="col-8">
          <card>
            <template slot="header">
              <h4 class="card-title">Productos Disponibles</h4>
            </template>
            <div class="product-list">
              <div v-for="product in products" :key="product.id" class="product-item">
                <img v-if="product.imgProduct" :src="getImageUrl(product.imgProduct)" class="card-img-top" alt="Imagen del Inventario">
                <h5>{{ product.nombre }}</h5>
                <p>Precio: {{ product.precio | currency }}</p>
                <button @click="addToCart(product)" class="btn btn-primary">Agregar al Carrito</button>
              </div>
            </div>
          </card>
        </div>
        <!-- Carrito de compras -->
        <div class="col-4">
          <card>
            <!-- Campo para buscar por código de barras -->
            <div class="barcode-search">
              <base-input type="text" label="Buscar por Código de Barras" placeholder="Código de Barras"
                v-model="barcodeInput" @input="searchByBarcode">
              </base-input>
            </div>

            <template slot="header">
              <h4 class="card-title">Carrito de Compras</h4>
            </template>
            <div v-if="cart.length === 0">
              <p>El carrito está vacío</p>
            </div>
            <div v-else>
              <ul class="cart-list">
                <li v-for="(item, index) in cart" :key="item.product.id">
                  <div class="cart-item">
                    <div>
                      {{ item.product.nombre }} - Precio: {{ item.product.precio | currency }}
                    </div>
                    <div>
                      <input type="number" v-model.number="item.quantity" min="1" class="form-control quantity-input">
                      <button @click="removeFromCart(index)" class="btn btn-danger btn-sm">Eliminar</button>
                    </div>
                  </div>
                </li>
              </ul>
              <div class="cart-total">
                <p>Total: {{ cartTotal | currency }}</p>
                <label for="payment">Pago del Cliente:</label>
                <input type="number" id="payment" v-model.number="payment" class="form-control">
                <p>Cambio: {{ change | currency }}</p>
              </div>
              <div class="cart-actions">
                <button @click="finalizeSale" class="btn btn-success">Finalizar Venta</button>
              </div>
            </div>
          </card>
        </div>
      </div>
    </div>

    <!-- Modal para mostrar el ticket -->
    <div class="modal" tabindex="-1" role="dialog" v-if="showTicket">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Ticket de Compra</h5>
            <button type="button" class="close" @click="closeTicket">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <!-- Sección para el ticket del cliente -->
          <div class="modal-body">
            <p>Gracias por su compra. Aquí está su ticket:</p>
            <p>Número de Orden: {{ orderNumber }}</p>
            <ul>
              <li v-for="item in cart" :key="item.product.id">
                {{ item.product.nombre }} - {{ item.quantity }} x {{ item.product.precio | currency }}
              </li>
            </ul>
            <p>Total: {{ cartTotal | currency }}</p>
            <p>Pago del Cliente: {{ payment | currency }}</p>
            <p>Cambio: {{ change | currency }}</p>
            <button class="btn btn-primary" @click="printClientTicket">Imprimir para Cliente</button>
          </div>
          <!-- Sección para la comanda -->
          <div class="modal-footer justify-content-center">
            <button type="button" class="btn btn-danger text-center" @click="closeTicket">Cerrar</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script src="https://cdn.jsdelivr.net/npm/qz-tray"></script>
<script>
import Card from 'src/components/Cards/Card.vue';
import axios from 'axios';
import qz from 'qz-tray'
import moment from 'moment';

export default {
  components: {
    Card,
    qz,
    moment
  },
  data() {
    return {
      products: [], // Inicialmente la lista de productos estará vacía
      categories: [
        { value: 'Bebidas', text: 'Bebidas' },
        { value: 'Helados', text: 'Helados' },
        { value: 'Troles', text: 'Troles' },
        { value: 'Otros', text: 'Otros' },
      ],
      cart: [],
      payment: 0, // Pago del cliente
      showTicket: false, // Controlar la visibilidad del ticket
      orderNumber: null, // Número de orden generado
      barcodeInput: '',
    };
  },
  created() {
    // Al iniciar el componente, cargar los productos desde la API
    this.loadProducts();
  },
  computed: {
    cartTotal() {
      return this.cart.reduce((total, item) => total + item.product.precio * item.quantity, 0);
    },
    change() {
      return this.payment - this.cartTotal;
    }
  },
  methods: {
    getImageUrl(imgProduct) {
      // Creamos un blob a partir del buffer
      const blob = new Blob([new Uint8Array(imgProduct.data)], { type: 'image/jpeg' });
      // Creamos una URL para el blob
      return URL.createObjectURL(blob);
    },
    loadProducts() {
      // Realizar la solicitud HTTP GET para obtener los productos desde la API
      axios.get('http://localhost:3000/api/products')
        .then(response => {
          // Asignar los productos recibidos a la propiedad 'products'
          this.products = response.data;
        })
        .catch(error => {
          console.error('Error al cargar los productos:', error);
          // Manejar el error, por ejemplo, mostrar un mensaje de error al usuario
        });
    },
    printClientTicket() {
      // Configurar opciones de impresión
      var config = qz.configs.create({
        printers: [{ name: "NombreDeTuImpresoraTermica" }],
        format: 'plain_text'
      });

      // Contenido del ticket del cliente
      var content = `
        Gracias por su compra. Aquí está su ticket:

        Número de Orden: 3

        Helado de Vaso - 1 x $20.50
        Helado de Cono Grande Sabor Vainilla - 4 x $17.50
        Total: $90.50

        Pago del Cliente: $100.00

        Cambio: $9.50
      `;

      // Enviar el contenido a la impresora
      qz.print(config, [{ type: 'raw', data: content }]);
    },
    searchByBarcode() {
      const product = this.products.find(product => product.barcode === this.barcodeInput);
      if (product) {
        this.addToCart(product);
        this.barcodeInput = ''; // Limpiar el input después de agregar el producto
      } else {
        alert('Producto no encontrado');
      }
    },

    addToCart(product) {
      const cartItem = this.cart.find(item => item.product.id === product.id);
      if (cartItem) {
        cartItem.quantity++;
      } else {
        this.cart.push({ product, quantity: 1 });
      }
    },
    removeFromCart(index) {
      this.cart.splice(index, 1);
    },
    finalizeSale() {
      if (this.payment > 0) {
        this.orderNumber = this.generateOrderNumber();
        // Realizar la venta de transacción
        this.finalizeTransaction();
      } else {
        alert("Por favor, el pago no esta definido, introduzca el pago antes de generar el ticket.");
      }
    },
    closeTicket() {
      this.showTicket = false; // Ocultar el ticket
      this.cart = []; // Limpiar el carrito después de cerrar el ticket
      this.payment = 0; // Reiniciar el pago
    },
    generateOrderNumber() {
      // Lógica para generar un número de orden único
      // Por ejemplo, podrías incrementar un contador cada vez que se finaliza una venta
      return this.cart.length + 1; // Ejemplo simple de generación de número de orden
    },
    finalizeTransaction() {
      // Realizar la solicitud HTTP para agregar una venta de transacción
      axios.post('http://localhost:3000/api/ventas_transaccion/', {
        precio: this.cartTotal,
        productos: this.cart,
        orden: this.orderNumber,
        nombrecliente: 'Cliente', // Puedes cambiar esto según necesites
        fecha_transaccion: moment().format('YYYY-MM-DD HH:mm:ss'),
        dinero: this.payment,
        codigo: 'Código', // Puedes cambiar esto según necesites
        cambio: this.change
      })
        .then(response => {
          console.log('Venta de transacción exitosa:', response.data);
          // Mostrar el ticket después de una venta de transacción exitosa
          this.showTicket = true;
        })
        .catch(error => {
          console.error('Error al realizar la venta de transacción:', error);
          alert('Hubo un error al realizar la venta de transacción. Por favor, inténtalo de nuevo.');
        });
    }
  },
  filters: {
    currency(value) {
      return new Intl.NumberFormat('es-MX', { style: 'currency', currency: 'MXN' }).format(value);
    },
  },
};
</script>

