<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-xl-4 col-md-6">
          <stats-card>
            <div slot="header" class="icon-warning">
              <i class="nc-icon nc-single-02 text-warning"></i>
            </div>
            <div slot="content">
              <p class="card-category">Clientes del día</p>
              <h4 class="card-title">{{ totalSales }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-refresh"></i>Updated now
            </div>
          </stats-card>
        </div>

        <div class="col-xl-4 col-md-6">
          <stats-card>
            <div slot="header" class="icon-success">
              <i class="nc-icon nc-chart-bar-32 text-success"></i>
            </div>
            <div slot="content">
              <p class="card-category">Total en pesos</p>
              <h4 class="card-title">${{ totalRevenue }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-calendar-o"></i>Last day
            </div>
          </stats-card>
        </div>

        <div class="col-xl-4 col-md-6">
          <stats-card>
            <div slot="header" class="icon-danger">
              <i class="nc-icon nc-delivery-fast text-danger"></i>
            </div>
            <div slot="content">
              <p class="card-category">Productos Totales</p>
              <h4 class="card-title">{{ totalProducts }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-clock-o"></i>Last day
            </div>
          </stats-card>
        </div>
      </div>
      <div class="row">
        <div class="col-md-8">
          <chart-card :chart-data="lineChart.data" :chart-options="lineChart.options"
            :responsive-options="lineChart.responsiveOptions">
            <template slot="header">
              <h4 class="card-title">Ventas por hora</h4>
              <p class="card-category">24 Hours performance</p>
            </template>
            <template slot="footer">
              <div class="legend">
                <i class="fa fa-circle text-info"></i> Ventas
              </div>
              <hr>
              <div class="stats">
                <i class="fa fa-history"></i> Updated 3 minutes ago
              </div>
            </template>
          </chart-card>
        </div>

        <div class="col-md-4">
          <chart-card :chart-data="pieChart.data" chart-type="Pie">
            <template slot="header">
              <h4 class="card-title">Productos más vendidos</h4>
              <p class="card-category">Últimas ventas</p>
            </template>
            <template slot="footer">
              <div class="legend">
                <i class="fa fa-circle text-info"></i> Nombre del Producto
              </div>
              <hr>
              <div class="stats">
                <i class="fa fa-clock-o"></i> Últimas ventas
              </div>
            </template>
          </chart-card>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6">
          <card>
            <template slot="header">
              <h5 class="title">Tareas</h5>
              <p class="category">Reabastecer productos bajos en stock</p>
            </template>
            <l-table :data="tableData.data" :columns="tableData.columns">
              <template slot="columns"></template>

              <template slot-scope="{row}">
                <td>
                  <base-checkbox v-model="row.checked"></base-checkbox>
                </td>
                <td>{{ row.title }}</td>
                <td class="td-actions text-right">
                  <button type="button" class="btn-simple btn btn-xs btn-info" v-tooltip.top-center="editTooltip">
                    <i class="fa fa-edit"></i>
                  </button>
                  <button type="button" class="btn-simple btn btn-xs btn-danger" v-tooltip.top-center="deleteTooltip">
                    <i class="fa fa-times"></i>
                  </button>
                </td>
              </template>
            </l-table>
            <div class="footer">
              <hr>
              <div class="stats">
                <i class="fa fa-history"></i> Updated 3 minutes ago
              </div>
            </div>
          </card>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ChartCard from 'src/components/Cards/ChartCard.vue'
import StatsCard from 'src/components/Cards/StatsCard.vue'
import LTable from 'src/components/Table.vue'
import axios from 'axios';

export default {
  components: {
    LTable,
    ChartCard,
    StatsCard
  },
  data() {
    return {
      editTooltip: 'Editar tarea',
      deleteTooltip: 'Eliminar tarea',
      totalSales: 0, // Ejemplo de cantidad total de ventas realizadas en el día
      totalRevenue: 0, // Ejemplo de ingreso total del día basado en las ventas
      totalProducts: 0, // Ejemplo de cantidad total de productos en stock
      totalClients: 45, // Ejemplo de total de productos vendidos en el día
      pieChart: {
        data: {
          // Ejemplo de datos para la gráfica de pastel de productos más vendidos
          labels: [],
          series: []
        }
      },
      lineChart: {
        data: {
          // Ejemplo de datos para la gráfica de líneas de ventas por hora
          labels: ['9:00AM', '12:00AM', '3:00PM', '6:00PM', '9:00PM', '12:00PM', '3:00AM', '6:00AM'],
          series: [
            [287, 385, 490, 492, 554, 586, 698, 695] // Ejemplo de ventas por hora
          ]
        },
        options: {
          low: 0,
          high: 800,
          showArea: false,
          height: '245px',
          axisX: {
            showGrid: false
          },
          lineSmooth: true,
          showLine: true,
          showPoint: true,
          fullWidth: true,
          chartPadding: {
            right: 50
          }
        },
        responsiveOptions: [
          ['screen and (max-width: 640px)', {
            axisX: {
              labelInterpolationFnc(value) {
                return value[0]
              }
            }
          }]
        ]
      },
      tableData: {
        data: [
          // Ejemplo de datos para las tareas (se pueden llenar dinámicamente según el stock de productos)
          { title: 'Reabastecer producto X', checked: false },
          { title: 'Reabastecer producto Y', checked: true },
          { title: 'Reabastecer producto Z', checked: false }
        ]
      }
    }
  },
  mounted() {
    axios.get('http://localhost:3000/api/productos_mas_vendidos')
      .then(response => {
        const data = response.data;
        console.log(data)

        const labels = data.map(item => item.nombre || 'Desconocido'); // Usar 'Desconocido' si el nombre es null
        const series = data.map(item => item.cantidad_vendida);

        // Actualizar los datos del gráfico de pastel
        this.pieChart.data.labels = labels;
        this.pieChart.data.series = series;
      })
      .catch(error => {
        console.error('Error al obtener los datos de los productos más vendidos:', error);
      });
    axios.get('http://localhost:3000/api/ventas/dia')
      .then(response => {
        const data = response.data;
        this.totalSales = data.cantidad_ventas;
      })
      .catch(error => {
        console.error('Error al obtener los datos de los productos más vendidos:', error);
      });


    axios.get('http://localhost:3000/api/ventas/dinero')
      .then(response => {
        const data = response.data;
        this.totalRevenue = data.total_ventas;
      })
      .catch(error => {
        console.error('Error al obtener los datos de los productos más vendidos:', error);
      });

    axios.get('http://localhost:3000/api/productos/total')
      .then(response => {
        const data = response.data;
        this.totalProducts = data.total_productos;
      })
      .catch(error => {
        console.error('Error al obtener los datos de los productos más vendidos:', error);
      });
  },

}
</script>

